.App .ant-btn{
  border-radius: 0;
  padding: 0 30px;
  margin-right: 15px;
  background-color: #1F8B4C;
  border: none;
  border-radius: 15px;
}

.App  .ant-btn-primary:hover, 
.App  .ant-btn-primary:focus {
  background-color: #4ca270;
  border-color: none;
}

.App .ant-menu-horizontal > .ant-menu-item:hover, 
.App .ant-menu-horizontal > .ant-menu-submenu:hover, 
.App .ant-menu-horizontal > .ant-menu-item-active, 
.App .ant-menu-horizontal > .ant-menu-submenu-active, 
.App .ant-menu-horizontal > .ant-menu-item-open, 
.App .ant-menu-horizontal > .ant-menu-submenu-open, 
.App .ant-menu-horizontal > .ant-menu-item-selected, 
.App .ant-menu-horizontal > .ant-menu-submenu-selected{
  color: #1F8B4C;
  border-bottom: 2px solid #1F8B4C;
}

.App .ant-menu-horizontal > .ant-menu-item-selected > a,
.App .ant-menu-horizontal > .ant-menu-item > a:hover{
  color: #1F8B4C;
}

.App h4, p{
  font-size: 16px;
  margin-bottom: 20px;
}
#header {
  /*box-shadow: 0px 0px 5px 0px #555;*/
}

#header .navbar {
  border: none;
  min-height: 50px;
  margin-bottom: 0px;
  /*box-shadow: 0px 0px 5px 0px #999;*/
}

#header .navbar-inverse{
  background-image: none;
  border-radius: 0px;
}

#header .navbar-brand, .navbar-nav li a{
  text-shadow: none;
}

#header .navbar-inverse{
  background-color: #fff;
  padding-top: 9px;
}

#header .navbar-inverse .navbar-nav .active a{
  color: #333;
  font-weight: 700;
  background-image: linear-gradient(to bottom, #fff 0%, #fff 100%);
  box-shadow: none;
  /* border-bottom: 5px solid #1F8B4C; */
}

#header .navbar-inverse .navbar-nav li a{
  font-size: 16px;
  color: #999;
  text-shadow: none;
  padding-top: 20px;
  padding-bottom: 20px;
}

#header .navbar-inverse .navbar-toggle {
  background-color: #fff;
  border-color: #fff;
}

#header .navbar-inverse .navbar-toggle .icon-bar {
  background-color: #333;
}

#header .navbar-inverse .navbar-toggle::hover{
  background-color: #fff;
}

#header .navbar-inverse .navbar-toggle:focus{
  background-color: #fff;
}

#header .navbar-inverse .navbar-collapse {
  border: none;
}

.sub-nav-bar {
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: white;
  display: block;
  box-shadow: 0px 1px 5px 0px #ccc;
}

.sub-nav-items {
  display: inline-block;
  vertical-align: middle;
}

.sub-nav-item {
  color: #555;
  padding-right: 30px;
  display: inline-block;
}

.sub-nav-bar a:hover {
    color: #1F8B4C;
}

.sub-nav-bar a:active {
    color: #1F8B4C;
}

.sub-nav-bar a:link {
    color: #555;
    text-decoration: none;
}

.sub-nav-bar a:visited {
    color: #555;
}

#sticky-nav {
  z-index: 999;
}

@media (max-width: 768px){
    .sub-nav-bar {
      display: none;
    }
}
.footer {
  background-color: white;
  color: #666;
  text-align: center;
  padding: 20px 0 15px;
}
.Home {
}



#section-about {
  text-align: center;
}

#introduction {
  padding: 100px 10% 120px;
}

#hello {
  color: #1F8B4C;
  font-size: 50px;
}

#hello:after {
  content:"Hello!";
  -webkit-animation: greetings 10s linear infinite; /* Safari 4+ */ /* Fx 5+ */ /* Opera 12+ */
  animation:         greetings 10s linear infinite; /* IE 10+, Fx 29+ */
}

@-webkit-keyframes greetings {
  0%   { content: "Hello!"; }
  20%   { content: "你好！"; }
  40%   { content: "Namaste!"; }
  60%   { content: "Grüezi!"; }
  80% { content: "Hej!"; }
}

@keyframes greetings {
  0%   { content: "Hello!"; }
  20%   { content: "你好！"; }
  40%   { content: "Namaste!"; }
  60%   { content: "Grüezi!"; }
  80% { content: "Hej!"; }
}

.call-to-actinon {
  margin-top: 30px;
}

.call-to-actinon h5 {
  font-weight: 700;
}

